}
* {
  box-sizing: border-box;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  min-height: 100vh;
}
.btn-primary {
  background: linear-gradient(135deg, var(--uh-red) 0%, var(--uh-red-dark) 100%);
  color: white;
  padding: 12px 24px;
  border-radius: 8px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 4px 14px rgba(200, 16, 46, 0.3);
}
.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(200, 16, 46, 0.4);
}
.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}
.btn-secondary {
  background: var(--background-tertiary);
  color: var(--foreground);
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: 500;
  border: 1px solid var(--border);
  cursor: pointer;
  transition: all 0.2s ease;
}
.btn-secondary:hover {
  background: var(--border);
}
.card {
  background: var(--background-secondary);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 24px;
}
.input {
  background: var(--background-tertiary);
  border: 1px solid var(--border);
  border-radius: 8px;
  padding: 12px 16px;
  color: var(--foreground);
  font-size: 16px;
  width: 100%;
  transition: border-color 0.2s ease;
}
.input:focus {
  outline: none;
  border-color: var(--uh-red);
}
.input::placeholder {
  color: var(--foreground-muted);
}
.status-open {
  color: var(--success);
}
.status-closed {
  color: var(--foreground-muted);
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-fade-in {
  animation: fadeIn 0.3s ease forwards;
}
@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(200, 16, 46, 0.3);
  }
  50% {
    box-shadow: 0 0 40px rgba(200, 16, 46, 0.5);
  }
}
.glow-pulse {
  animation: pulse-glow 2s ease-in-out infinite;
}
.hero-gradient {
  background: radial-gradient(ellipse 80% 50% at 50% -20%, rgba(200, 16, 46, 0.3), transparent);
}
